import React, { useState, useEffect, useContext } from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import queryString from "query-string"
import axios from 'axios';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import { useHistory } from 'react-router-dom';
import { useStyles } from './ClassesSetupStyle';
import ClassDetails from './ClassDetails';
import RelatedSections from './RelatedSections';
import { titleContext } from '../../../components/context/Context'

const ClassTab = () => {
  const history = useHistory();
  const classes = useStyles();
  const queryParams = queryString.parse(window.location.search)

  let unmounted = false;

  const [value, setValue] = useState('1');
  const [classDetails, setClassDetails] = useState({});
  const [classupdated, setClassUpdated] = useState(false);
  const [titleVal , setTitleVal] = useContext(titleContext);

  useEffect(() => {
    getClassDetails();
  }, []);

  useEffect(() => {
    setTitleVal(classDetails?.name);
    setClassUpdated(true);
    setTimeout(() => {
      setClassUpdated(false);
    }, 0);
  }, [classDetails])

  const getClassDetails = async () => {
    unmounted = false;
    const source = axios.CancelToken.source();
    await axios.get(`${process.env.REACT_APP_SERVER}/class-section-by-id/${queryParams.division}`)
      .then((response) => {
        console.log(response.data)
        setClassDetails(response.data);
      })
      .catch((error) => {
        console.log(error);
        if (!unmounted) {
          if (error.request.status === 403) {
            localStorage.removeItem('userDetail');
            localStorage.removeItem('userToken');
            localStorage.removeItem('activeSubscription');
            history.replace('/login');
                  history.go(0);
          }
        }
      })
      .finally(() => {
        return function () {
          unmounted = true;
          source.cancel("Cancelling in cleanup");
        };
      });
  }

  const handleChange = (event, newValue) => {
    console.log(newValue)
    setValue(newValue);
  };
  return (
    <Box sx={{ width: '100%', typography: 'body1' }}>
      <TabContext value={value}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <TabList variant='scrollable' onChange={handleChange} aria-label="lab API tabs example">
            <Tab label="Details" value="1" />
            <Tab label="Related" value="2" />
          </TabList>
        </Box>
        <TabPanel className={classes.tabPanel} value="1">
          { !classupdated &&
            <ClassDetails classInfo={classDetails}  handleRefresh = {getClassDetails}/>
          }</TabPanel>
        <TabPanel className={classes.tabPanel} value="2"><RelatedSections classInfo={classDetails} updateDetails={handleChange} /></TabPanel>
      </TabContext>
    </Box>
  )
}

export default ClassTab